html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  background-color: var(--color-white);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

.list-reset {
  list-style: none;
  padding: 0;
  margin: 0;
}

.is-outer {
  position: relative !important; // stylelint-disable-line declaration-no-important
  left: -9999px !important; // stylelint-disable-line declaration-no-important
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}

.for-mobile {
  display: none;

  @include mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important; // stylelint-disable-line declaration-no-important
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1150px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.title{
  color: var(--color-white);
  font-weight: 800;
  font-size: 55px;
  line-height: 130%;
}

.tittle{
  font-weight: bold;
  font-size: 40px;
  line-height: 130%;
  color: var(--color-white);
}

.grid{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}


.slick-dots{
  display: none;
}

